import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { mouse } from './cMouse';
const { ccclass, property } = _decorator;

const GAME_COMBINATIONS: any[] = [
    { 3: 3 },		// hand cards no fix line 0
    { 3: 5 },		// hand cards no fix line 1
    { 3: 8 },		// hand cards no fix line 2
    { 3: 10 },		// hand cards no fix line 3
    { 3: 25 },	    // hand cards no fix line 4
    { 3: 100 },	    // hand cards no fix line 5
    { 3: 300 },	    // hand cards no fix line 6
    { 3: 0 },	    // hand cards no fix line 7
];
@ccclass('mouseDetail')
export class mouseDetail extends Component {
    @property({ type: Node, tooltip: "" })
    public roleSpriteNode: Node = null!;
    @property({ type: Node, tooltip: "" })
    public BGNode: Node = null!;
    @property({ type: Node, tooltip: "" })
    public BGNode2: Node = null!;
    @property({ type: Node, tooltip: "" })
    public numNode: Node = null!;
    @property({ type: Node, tooltip: "" })
    public specialNode: Node = null!;

    init (idx: any, isLeft: any) {
        if (mouse.detail) {
            mouse.detail.node.active = false;
        }
        mouse.detail = this;
        let id = idx;
        this.roleSpriteNode.children[idx - 1].active = true;

        for (let i in GAME_COMBINATIONS[id - 1]) {
            (((this.numNode.getChildByName(i) as Node).children[0] as Node).getComponent(Label) as Label).string = GAME_COMBINATIONS[idx - 1][i];
        }
        //
        if (!isLeft) {
            this.BGNode.active = false;
            this.BGNode2.active = true;
            this.numNode.x = -250;
            this.specialNode.x = -340;
        }
    }

    node_onclick () {
        mouse.node.deskMask.active = false;
        this.node.destroy();
        mouse.detail = null;
    }

}