import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('mouseAudio')
export class mouseAudio extends Component {
    @property([AudioClip])
    public bgmAudio = [];
    @property(AudioClip)
    public stopWheel = null!;
    @property(AudioClip)
    public bigWinEf = null!;
    @property(AudioClip)
    public clickstart = null!;
    @property(AudioClip)
    public clickOpen = null!;
    @property(AudioClip)
    public clickClose = null!;

    onLoad() {

    }

    start() {
        this.playBgm();
    }

    stopbigwinmain() {
        this.playBgm();
    }

    stopAudio() {
        tgx.AudioMgr.inst.audio.stopAll();
    }

    private url: string = "sound/bgm_mg";
    playBgm() {
        tgx.AudioMgr.inst.audio.playMusicLoop(this.url, ModuleDef.SLOTS_MOUSE)
    }

    playStopWheel() {
        this.playEf(this.stopWheel);
    }

    private BWurl: string = "sound/GeneralAudio";
    playBW() {
        // tgx.AudioMgr.inst.audio.playMusic(this.BWurl,()=>{},ModuleDef.SLOTS_MOUSE);
    }

    playEf(clip: any) {
        tgx.AudioMgr.inst.audio.playEffect(clip);
    }

    playclickstart() {
        this.playEf(this.clickstart);
    }

    playclickopen() {
        this.playEf(this.clickOpen);
    }

    playclickclose() {
        this.playEf(this.clickClose);
    }

    clearAll(){
        tgx.AudioMgr.inst.audio.clearAll();
    }
}